@import url(https://fonts.googleapis.com/css2?family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Encode+Sans&display=swap);


section{
     height: 90vh;

    
}
.hero-image {
     background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)), url("https://demo.wenthemes.com/travel-gem/wp-content/uploads/sites/65/2018/10/banner-9.jpg");
     height: 100%;
     background-position: center;
     background-repeat: no-repeat;
     background-size: cover;
     position: relative;
   }
   
   .hero-text {
     text-align: center;
     position: absolute;
     top: 50%;
     left: 50%;
     -webkit-transform: translate(-50%, -50%);
             transform: translate(-50%, -50%);
     color: white;
     font-family: 'Pacifico', cursive;
   }
   
   .hero-text button {
     border: none;
     outline: 0;
     display: inline-block;
     padding: 10px 25px;
     color: black;
     background-color: #ddd;
     text-align: center;
     cursor: pointer;
   }
.w-40{
  width: 300px;
  height: 400px;
  margin: auto;
}
footer{
  background-color:rgb(0, 0, 0);
  padding: 20px;
  text-align: center;
  color: wheat;
}
#team{

background-color: rgb(75, 63, 63);

}

.card-wrapper{
     padding: 10px;

}
.details{
     padding: 20px;
     text-align: center;
     color: antiquewhite;
}

.card-img{
     padding:10px;
    
}
.card-img img{
     border-radius: 20px;
    
}
.banner{
     height: 50vh;
     background-color: yellow;
     background-image: url("https://demo.wenthemes.com/travel-gem/wp-content/themes/travel-gem/images/default-banner.jpg");
     width: 100%;
     background-size: cover;
}

.banner h1{
   
     color: white;
     font-weight: 900 !important;
     padding-top: 100px;
     text-align: center;     

}

#contact{
      
      background-image: url("https://demo.wenthemes.com/travel-gem/wp-content/uploads/sites/65/2018/10/banner-6.jpg");
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      font-family: 'Encode Sans', sans-serif;
 }
 .form-card{
      background-color:rgb(170, 167, 167),transparent;
      opacity: 0.3px;
      padding: 10px;
      width: 40%;
      box-shadow: 2px -10px 20px black;
      border-radius: 10px;
      color:white;
      font-weight: 800;  

 }
 .card-title{
      margin-top: 50px;
      padding: 10px;
      color:white;
   
 }
 @media screen and (max-width: 600px) {
     .form-card{
          width: 90%;
     }
     }

     label{
          width: 30%;
          background-color:black;
          padding: 5px;
          text-align: center;
     }
h1{
     font-family: 'Encode Sans', sans-serif;
}
p{
     font-family: 'Encode Sans', sans-serif;
}
.cards{
     background-color: yellow;
     height: 100vh;
     margin: 0 auto;
}

.card-width{
     float: left;
     margin: 10px;
     padding: 5px;
   
}
.card-content{   
     padding: 20px;
     text-align: center;
     font-family: 'Encode Sans', sans-serif;
}


img{
     width: 100%;
     height: 300px;
     vertical-align: middle;
     padding: 0;
     
}
@media screen and (min-width: 700px) {
    
     .card-width{
          width: 250px;
          vertical-align: middle;
          padding: 5px;
          cursor: pointer;
          
     }
     .card-width:hover{
          border-radius: 5px;
          box-shadow: 5px 5px 20px black;
        
     }
    }


#dummy{
     height: 50vh;
     background-image:linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 1)),url("https://demo.wenthemes.com/travel-gem/wp-content/uploads/sites/65/2018/10/banner-6.jpg");
     background-size: 100%;
     background-position: cover;
}

.dummy-content{
     width: 60%;
     margin: 0 auto;
     font-size: 20px;
     text-align: center;
}
.dummy-content p{
     margin-top: 50px;
}
@media screen and (max-width: 600px) {
    #tophead{
         display: none;
    }
   }
